.vacancies {
  padding: 40px 15px;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    padding: 75px 15px 150px;
  }

  .light-text {
    text-align: center;
    margin-top: 20px;

    @media screen and (min-width: $medium-screen) {
      margin-top: 130px;
    }
  }

  .bold-green {
    text-align: center;
    margin-top: 80px;
  }
}

.vacancies-wrapper {
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
  flex-wrap: wrap;

  @media screen and (min-width: $medium-screen) {
    justify-content: space-between;
    margin-top: 100px;
  }
}

.vacancies__item {
  flex: 0 1 300px;
  border-radius: 5px;
  border: 1px #D1DBE3 solid;
  height: 120px;
  padding: 25px 30px 0;
  text-align: center;
  transition: transform 0.3s;
  margin-bottom: 30px;

  @media screen and (min-width: $mobile-screen){
    flex: 0 1 170px;
    height: 170px;
    padding: 20px 10px 0;
  }

  @media screen and (min-width: $medium-screen) {
    margin-bottom: 0;
  }

  &:hover {
    border: 0;
    box-shadow: 2px 2px 30px 0 rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transform: translateY(-20px);

    &.is-open {
      box-shadow: none;
      transform: translateY(0);
      border: none;
      opacity: 1;
    }
  }
}

.vacancies__item-position {
  color: $forest-green;
  font-weight: bold;
  line-height: 2;
}

.vacancy-info {
  position: relative;
  width: 100vw;
  margin-left: 50%;
  top: 300px;
  transform: translateY(-60%) translateX(-50%);
  box-shadow: 2px 2px 50px 10px rgba(0, 0, 0, 0.1);
  padding: 70px 70px 30px;
  background-color: #F6FCF9;
  display: none;
  max-width: 1200px;

  .list {
    text-align: left;
    padding-left: 50px;
    max-width: 720px;
    margin-top: 10px;
  }

  .list__item {
    font-weight: 300;
    color: $grey;
    margin-top: 5px;
  }

  .list__item::before {
    left: -50px;
  }
}

.vacancy-info-header {
  display: flex;
}

.vacancy-info-header__item {
  color: $forest-green;
  letter-spacing: 0.5px;

  &:last-child {
    margin-left: 68px;

    img {
      margin-left: 5px;
    }
  }
}

.vacancy-info__position,
.vacancy-info__caption {
  font-size: 40px;
  font-weight: bold;
  line-height: 61px;
  max-width: 540px;
  letter-spacing: 1px;
  text-align: left;
  margin-top: 30px;
}

.vacancy-info__caption {
  font-weight: 200;
  color: $grey;
  margin-top: 30px;
  max-width: none;
  font-size: 35px;
}

.vacancy__btn {
  margin: 100px auto 0;
  width: 230px;
}

.vacancy__application {
  margin: 65px auto;
  background-color: #fff;
  box-shadow: 2px 2px 30px 0 rgba(0, 0, 0, 0.1);
  padding: 60px 3%;
  max-width: 570px;
}

.vacancy-form__details {
  display: flex;
  justify-content: space-between;
}

.vacancy-form__label {
  color: $grey;
  font-size: 14px;
  width: 47%;
  text-align: left;
  margin-top: 20px;
  display: block;
}

.vacancy-form__input {
  border: 1px solid #D1DBE3;
  background-color: #fff;
  height: 40px;
  padding: 10px;
  width: 100%;
  margin-top: 10px;
  border-radius: 5px;
}

.vacancy-form__mssg {
  height: 120px;
}

.resume__input {
  z-index: 10;
  display: none;
}

.resume {
  margin: 40px auto 0;
  text-align: center;
}

.resume__label {
  color: $forest-green;
  position: relative;
  cursor: pointer;

  &::before {
    content: '.txt, .doc, .pdf';
    position: absolute;
    color: $grey;
    width: 100%;
    top: 30px;
    left: 0;
  }
}

.vacancy-form__upload {
  margin: 60px auto 0;
  width: 170px;
}

.vacancy__arrow {
  margin-left: 40px;
  margin-bottom: 7px;
  width: 23px;
}

.select {
  width: 47%;

  select {
    border-radius: 20px;
    border: 1px #9197AE solid;
    top: 0;
  }
}

$background: #fff;
$select-color: #000;
$select-background: #fff;
$select-height: 40px;

.select-hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
  color: #B3B3B3;
}

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 12px;
  color: $select-color;
  height: $select-height;
  width: 100%;
  transition: all 0.25s;
}

.select-styled {
  font-size: 16px;
  border: 1px solid #d1dbe3;
  background-color: #fff;
  height: 40px;
  padding:  13px 10px;
  margin-top: 10px;
  border-radius: 5px;
  letter-spacing: 0.5px;
  color: #979797;

  &:after {
    content: url("../images/grey-arrow.svg");
    position: absolute;
    top: 22px;
    right: 18px;
    transition: transform 0.2s;
  }

  &:hover {
    background-color: #fff;
  }

  &:active, &.active {
    background-color: #fff;
    border-bottom: 0;

    &:after {
      transform: rotate(180deg);
    }
  }
}

.select-options {
  display: none;
  position: absolute;
  top: 115%;
  width: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #fff;
  letter-spacing: 0.5px;

  li {
    margin: 0;
    color: gray;
    font-size: 16px;
    padding: 11px 10px;
    border: 1px solid rgba(223, 223, 223, 0.83);
    transition: all 0.15s ease-in;

    &:hover {
      color: $forest-green;
    }

    &[rel="hide"] {
      display: none;
    }
  }
}
