.values {
  background-color: $light-green;
  padding: 40px 15px 90px;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    padding: 75px 15px 150px;
  }

  .text {
    font-weight: normal;

    .section-caption {

      @media screen and (min-width: $medium-screen) {
        font-size: 16px;
      }
    }
  }
}

.values-wrapper {
  display: flex;
  margin-top: 50px;
  flex-wrap: wrap;

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    margin-top: 115px;
  }
}

.values__item {
  flex: 0 1 340px;
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;

  @media screen and (min-width: $mobile-screen) {
    flex: 0 1 220px;
    margin-right: 30px;
  }

  @media screen and (min-width: $medium-screen) {
    margin-bottom: 0;
  }
}

.values__item-heading,
.text--slider {
  color: $forest-green;
  text-transform: uppercase;
  line-height: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  letter-spacing: 2px;
  height: 20px;

  @media screen and (min-width: $medium-screen) {
    margin-bottom: 40px;
  }
}

.values__item-heading {
  font-weight: bold!important;
  line-height: 1.2;
}

.values-slider {
  max-width: 870px;
  margin: 65px auto 0;

  .arrow-link {
    bottom: -54px;

    @media screen and (min-width: $medium-screen) {
      bottom: -90px;
    }
  }

  .arrow-link--next {
    right: 35%;

    @media screen and (min-width: $tablet-screen) {
      right: 44%;
    }
  }

  .arrow-link--prev {
    left: 35%;

    @media screen and (min-width: $tablet-screen) {
      left: 44%;
    }
  }
}

.text--slider {
  margin: 20px auto 30px;
  display: block;
  text-align: center;
  font-weight: bold!important;

  @media screen and (min-width: $medium-screen) {
    margin: 80px 50px 30px 0;
  }
}