.contact-us {
  .intro__text {
    max-width: 340px;
  }

  .contacts__info {
    max-width: none;
  }

  .contacts__heading {
    margin: 60px 0 23px;
  }

  .contacts-block {
    text-align: left;
    margin-top: 60px;
    padding: 0;
  }

  .contacts-form {
    max-width: 400px;
    margin-top: 55px;
  }

  .intro__img::before {
    display: none;
  }

  .intro__img--animated {
    right: -15%;
    top: 170px;

    @media screen and (min-width: $tablet-screen){
      top: 210px;
    }
  }

  .intro__shadow {
    top: 70px;
    max-width: 70%;
    right: -18%;
  }
}

@keyframes shadowBlur {
  0% {
    transform: translateY(0px);
    filter: blur(13px);
    opacity: 0.9;
  }

  50% {
    transform: translateY(0px);
    filter: blur(30px);
    opacity: 0.8;
  }

  100% {
    transform: translateY(0px);
    filter: blur(13px);
    opacity: 0.9;
  }
}

.contacts-info {
  padding: 40px 7% 120px;
}

.contacts {
  flex: 1 0 54%;
  margin-bottom: 25px;

  &:last-child {
    margin-bottom: 0;
  }

  @media screen and (min-width: $tablet-screen) {
    flex: 0 1 38.7%;
  }

  @media screen and (min-width: $medium-screen) {
    margin-bottom: 0;
  }
}

.contacts__heading {
  color: #BDBDBD;
  font-size: 16px;
  margin-bottom: 14px;

  @media screen and (min-width: $tablet-screen) {
    margin-bottom: 21px;
  }
}

.contacts__info {
  line-height: 37px;
  color: #000;
  max-width: 250px;

  &:hover {
    color: $forest-green;
  }
}

.contacts__email {
  max-width: 353px;
  transition: all 0.3s;

  &:hover {
    transform: scale(1.05);
    text-shadow: 4px 4px 30px rgba(142, 137, 145, 0.37);
  }
}

.burger__link {
  text-decoration: underline;
  font-weight: bold;
  color: #3191d0;

  &:hover {
    text-decoration: none;
  }
}

.order-now {
  max-width: 1000px;

  .contacts-block {
    @media screen and (min-width: $medium-screen){
      margin-left: 30px;
    }
  }

  .contacts-block {
    padding: 0 15px 50px;

    @media screen and (min-width: $medium-screen){
      padding: 0 15px 130px;
    }
  }
}