.burger-wrapper {
  position: fixed;
  display: flex;
  justify-content: space-between;
  min-width: 1600px;
  margin: 0 0 0 50%;
  transform: translateX(-50%);

  .header-hidden {
    margin-left: 50%;
    transform: translateX(-50%);
  }
}

.burger-menu__btn {
  position: fixed;
  right: 2.9vw;
  top: 27px;
  cursor: pointer;
  z-index: 1;
  transition: transform 0.4s;
  outline: 0;

  &:hover {
    transform: scale(1.3);
  }

  @media screen and (min-width: $medium-screen) {
    top: 45px;
  }
}

.header-hidden {
  flex-wrap: wrap;
  position: fixed;
  height: 70px;
  width: 100vw;
  display: flex;
  z-index: 11;
  background-color: rgba(255, 255, 255, 0.75);
  transition: all 0.7s ease-in;

  @media screen and (min-width: $medium-screen) {
    background-color: transparent;
    top: -100px;
  }

  &.off-canvas  {
    top: 0;
  }

  &.fixed {
    z-index: 9999;
    display: flex;
  }

  .logo {
    position: fixed;
    z-index: 2;
    left: 20px;
    max-width: 130px;
    top: 20px;

    @media screen and (min-width: $medium-screen){
      top: 35px;
      left: 35px;
      max-width: none;
      justify-content: space-around;
    }
  }

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    justify-content: space-between;
    display: none;
  }
}

.burger-menu__close {
  margin-left: auto;
  cursor: pointer;
  outline: 0;
  transform-origin: center;
  transition: transform 1s;
  right: 2.9vw;
  top: 20px;
  position: absolute;
  display: none;

  &:hover {
    transform: rotate(360deg);
  }

  @media screen and (min-width: $medium-screen) {
    top: 45px;
  }
}


.burger-menu {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  background: url("../images/backgrounds/burger-bg.png") no-repeat;
  background-size: 100%;
  z-index: 10;

  .is-hidden {
    left: auto!important;
  }

  .container {
    padding: 0 15px;

    @media screen and (min-width: $medium-screen){
      padding: 8% 15px;
    }
  }

  .navigation {
    flex-direction: column;
    margin-left: 10%;
    flex: 1 0 70%;
    position: relative;
    margin-top: 80px;

    @media screen and (min-width: $medium-screen) {
      margin: 0;
    }
  }

  .navigation__link {
    color: $forest-green;
    font-size: 36px;
    line-height: 1.1;
    font-weight: 900;
    margin-top: 25px;
    letter-spacing: 0.45rem;
    transition: all 0.25s ease-out;

    &:hover {
      transform: translateX(-30px);
      color: #000;
    }
  }

  .footer-social {
    margin: 30px 0 0;

    @media screen and (min-width: $medium-screen) {
      margin: 4vh 0 0 52%;
      transform: translateX(-40%);
    }
  }
}

.burger-container {
  height: 60vh;
  position: relative;
  display: flex;
  margin-top: 0;
  flex-wrap: wrap;

  @media screen and (min-width: $tablet-screen) {
    margin-top: 8vh;
  }

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
  }
}

.burger-options {
  flex: 1 0 24.7%;
  padding-top: 40px;
  margin-left: 10%;

  @media screen and (min-width: $medium-screen) {
    height: 120%;
    text-align: center;
    margin-left: 0;
  }

   .language {

     .button {
       margin: 75% auto 0;
       max-width: none;
     }
   }
}

.burger__link {
  font-weight: bold;
  text-decoration: none!important;
  color: #3191d0;
  padding: 12px 22px;
  border-radius: 25px;
  border: 2px solid #3191d0;

  &:hover {
    text-decoration: none;
  }
}