.about {

  .intro.container {
    max-width: 1200px;
  }

  .intro__shadow {
    top: 427px;
    right: -1%;
  }
}

.structure {
  background-color: $light-green;
  padding: 40px 15px;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    padding: 85px 15px 90px;
  }

  .container {
    max-width: 960px;
    margin: 0 auto;
  }

  .text {
    margin-top: 30px;
    line-height: 1.8;

    @media screen and (min-width: $medium-screen) {
      margin-top: 50px;
    }
  }

  .list {
    margin-top: 30px;
    padding-left: 3.5%;
    font-weight: bold;

    @media screen and (min-width: $medium-screen) {
      margin-top: 60px;
    }
  }

  .list__item {
    font-weight: bold;
  }
}

.hierarchy {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 10px;

  @media screen and (min-width: $mobile-small) {
    justify-content: space-between;
  }

  @media screen and (min-width: $medium-screen) {
    margin-top: 50px;
  }
}

.hierarchy__item {
  flex: 1 0 170px;
  text-align: center;
  padding: 25px 15px 10px;
  border: 1px solid #D1DBE3;
  border-radius: 5px;
  height: 170px;
  margin-top: 30px;
  align-content: center;
  max-width: 170px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  &:first-child {
    max-width: none;
    height: 200px;
    flex: 1 0 100%;
  }
}

.hierarchy__item-position {
  font-weight: bold;
  line-height: 25px;
}

.hierarchy__item-mail {
  color: $forest-green;
}

.navigation--about {
  position: fixed;
  left: -15%;
  z-index: 100;
  height: 181px;
  justify-content: space-between;
  top: 57%;
  flex-direction: column;

  .navigation__link {
    position: relative;

    &:hover {
      color: #000;
    }
  }
}