.intro {
  margin: -25px auto 0;
  position: relative;
  height: 400px;
  padding: 100px 25px 100px;

  @media screen and (min-width: $mobile-screen) {
    padding: 180px 25px;
    height: 580px;
  }

  &::before {
    content: '';
    height: 60%;
    width: 100%;
    position: absolute;
    top: 145px;
    left: 0;
    background-color: $light-green;

    @media screen and (min-width: $mobile-screen) {
      width: 70%;
      transform: translateX(-50%);
      left: 50%;
      height: 70%;
    }
  }
}

.intro__wrapper {
  position: relative;
}

.intro__text {
  font-size: 36px;
  font-weight: bold;
  line-height: 36px;
  max-width: 700px;
  color: #000;

  @media screen and (min-width: $tablet-screen) {
    font-size: 64px;
  }
}

.intro__header {
  display: block;
  font-size: 40px;
  text-transform: capitalize;
  color: #000;
  line-height: 55px;
  max-width: 200px;
  margin-top: 100px;

  @media screen and (min-width: $tablet-screen) {
    font-size: 64px;
    line-height: 70px;
    max-width: 375px;
  }
}

.slide__text-header {
  display: block;
  margin-bottom: 25px;
  font-size: 48px;

  @media screen and (min-width: $tablet-screen) {
    font-size: 64px;
  }
}

.main-page {
  .intro__text {
    font-size: 14px !important;
    line-height: 25px;
    max-width: 570px;
    font-weight: normal;
    color: #000;

    @media screen and (min-width: $tablet-screen) {
      font-size: 18px!important;
      line-height: 36px;
    }
  }

  .intro {
    margin-top: -24px;
    height: 530px;

    @media screen and (min-width: $mobile-screen) {
      height: 550px;
    }

    &::before {
      height: 45%;

      @media screen and (min-width: $mobile-screen) {
        height: 60%;
      }

      @media screen and (min-width: $tablet-screen) {
        height: 75%;
      }
    }
  }

  .intro__img--animated {
    max-width: 38%;
    top: 10%;

    @media screen and (min-width: $mobile-screen) {
      top: 28%;
    }
  }

  .intro__header {
    display: block;
    font-size: 48px;
    max-width: 375px;
    text-transform: capitalize;
    color: #000;
    line-height: 70px;
    margin-bottom: 15px;
    margin-top: 55px;

    @media screen and (min-width: $tablet-screen) {
      font-size: 64px;
    }
  }

  .intro__shadow {
    display: none;

    @media screen and (min-width: $tablet-screen) {
      top: 240px;
      right: 2%;
      display: block;
    }
  }
}

.intro__img--animated {
  position: absolute;
  top: 160px;
  right: 2%;
  max-width: 55%;

  @media screen and (min-width: $tablet-screen) {
    animation: move 4s linear infinite;
    max-width: 55%;
  }
}

.intro__img--small {
  position: absolute;
  width: 35%;
  max-width: 290px;
  top: 0;
  right: -25px;

  @media screen and (min-width: $medium-screen) {
    top: -25px;
    right: 0;
  }
}

.intro__code {
  top: 300px;
  animation: move 4s linear infinite;
  animation-delay: 1s;
  right: 7%;
  position: absolute;
  display: none;

  @media screen and (min-width: $mobile-screen) {
    display: block;
  }
}

.intro__shadow {
  top: 240px;
  right: 2%;
  position: absolute;
  animation: shadow 4s linear infinite;
  z-index: -1;
}

@keyframes move {
  0% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-70px);
  }

  100% {
    transform: translateY(0px);
  }
}

@keyframes shadow {
  0% {
    opacity: 1;
    filter: blur(5px);
  }

  50% {
    filter: blur(50px);
  }

  100% {
    opacity: 1;
    filter: blur(5px);
  }
}