.services-page {
  .service-single {
    position: fixed;
    background-color: #fff;
    z-index: 100000;
    top: 50%;
    max-width: 90%;
    display: none;
    box-shadow: 11px 15px 40px 20px rgba(68, 68, 68, 0.1);
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
    width: 100%;
    padding: 52px 20px 30px;

    @media screen and (min-width: $medium-screen){
      max-width: 1170px;
      padding: 52px 20px;
    }

    .button {
      width: 230px;
      margin: 30px auto;

      @media screen and (min-width: $medium-screen){
       margin: 60px auto;
      }
    }
  }

  .services__description-heading {
    font-weight: bold;
    font-size: 20px;
    line-height: 1.2;
    margin-top: -30px;

    @media screen and (min-width: $medium-screen){
      line-height: 41px;
    }
  }

  .services__description-text {
    max-width: 580px;
    text-align: left;
    line-height: 1.5;
    font-size: 14px;
    font-weight: 300;
    margin: 25px auto 0;

    @media screen and (min-width: $medium-screen){
      font-size: 18px;
      line-height: 2;
    }
  }

  .services__description-article {
    margin-top: 80px;
  }
}