.trends {
  padding: 40px 15px;

  @media screen and (min-width: $medium-screen) {
    padding: 85px 15px;
  }
}

.trends-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 6%;
    justify-content: space-between;
  }
}

.trends-item {
  flex: 1 0 30%;
  border: 1px solid #D1DBE3;
  padding: 30px 20px 10px;
  position: relative;
  margin: 0 10px 30px;
  min-width: 290px;
  object-fit: cover;
  transition: all 0.25s;

  @media screen and (min-width: $tablet-screen){
    flex: 0 1 30%;
    min-width: 320px;
    padding: 30px 30px 10px;
  }

  @media screen and (min-width: $medium-screen){
    margin: 0 35px 30px 0;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    transform: translateY(-20px);
    border: 0;

    .trends-item__img {
      visibility: visible;
    }

    .trends-item__title,
    .trends-item__text {
      color: #fff;
    }

    .trends-item__link {
      background-color: $forest-green;
      color: #fff;
    }
  }
}

.trends-item__img {
  visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.trends-item__date {
  font-size: 16px;
  color: $forest-green;
  text-transform: lowercase;
  letter-spacing: 2px;
}

.trends-item__title,
.trends-item__text {
  margin-top: 35px;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.8;
}

.trends-item__text {
  margin-top: 20px;
  font-weight: 300;
}

.trends-item__link {
  margin: 30px auto;
  width: 200px;

  @media screen and (min-width: $medium-screen) {
    margin: 55px auto 30px;
  }
}

.more-trends {
  width: 150px;
  margin: 20px auto 0;

  @media screen and (min-width: $medium-screen) {
    margin: 30px auto 0;
  }
}