.hero {
  height: 100vh;
  min-height: 650px;
  position: relative;

  .container {
    padding-top: 20%;
    max-width: 1000px;
  }
}

.hero--main {
  display: flex;
  flex-direction: column;
  justify-content: center;

  .container {
    padding-top: 0;
    padding-left: 6%;
    max-width: 730px;
  }

  .hero__text {
    max-width: none;
  }
}

.hero__slogan {
  font-size: 2.25rem;
  max-width: 520px;
  font-weight: 900!important;
  letter-spacing: 7px;
  margin-bottom: 4rem;
  line-height: 1.1;

  strong {
    color: $forest-green;
    font-weight: 900;
  }
}

.hero__caption {
  font-weight: bold;
  line-height: 1.5;
  max-width: 490px;
  margin-top: 30px;
}

.hero__img {
  position: absolute;
  z-index: -1;
  width: 100%;
  object-fit: cover;
  height: 100%;
  top: 0;
  left: 0;
}

.hero__text {
  margin-top: 30px;
  line-height: 25px;
  max-width: 450px;
}

.hero__anchor {
  display: block;
  font-weight: bold;
  color: $forest-green;
  position: relative;
  margin-top: 110px;

  &:before {
    content: url(../images/arrow-down-anchor.svg);
    position: absolute;
    top: -15px;
    left: -70px;
  }

  img {
    vertical-align: middle;
    margin-right: 30px;
  }
}