.section-heading {
  font-size: 36px;
  font-weight: 900;
  text-align: center;
  line-height: 61px;
  text-transform: capitalize;
  letter-spacing: 0.45rem;
}

.section-caption {
  font-weight: 300;
  line-height: 2;
  text-align: center;
  color: $grey;
  max-width: 560px;
  margin: 25px auto 0;
  letter-spacing: 0.24px;

  @media screen and (min-width: $tablet-screen) {
    margin-top: 50px;
  }
}

.bold-green {
  font-size: 36px;
  font-weight: 900;
  line-height: 47px;
  display: block;
  color: $forest-green;
  letter-spacing: 0.45rem;
}

.text {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.8;
}