.services-wrapper {
  background-color: $light-green;
  padding: 40px 0 0;

  @media screen and (min-width: $medium-screen){
    padding: 83px 0 0;
  }

  .arrow-link {
    display: none!important;

    @media screen and (min-width: $mobile-screen) {
      bottom: -250px;
      display: block!important;
    }
  }

  .arrow-link--prev {
    left: 36.5%;

    @media screen and (min-width: $tablet-screen) {
      left: 42.5%;
    }

    @media screen and (min-width: 991px) {
      left: 45.5%;
    }
  }

  .arrow-link--next {
    right: 36.5%;

    @media screen and (min-width: $tablet-screen) {
      right: 42.5%;
    }

    @media screen and (min-width: 991px) {
      right: 45.5%;
    }
  }

  .slick-slide {
    text-align: center;
    margin-top: 50px;
    width: 120px;
    outline: 0;
    position: relative;
    cursor: pointer;

    @media screen and (min-width: $mobile-screen){
      width: 235px;
    }

    &::before {
      content: '';
      position: absolute;
      background-color: #fff;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      top: 0;
      left: 0;
      opacity: 0;
      box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
    }

    &.slick-center::before {
      opacity: 1;
      transition: opacity 1.5s ease-out;
    }
  }

  .slick-current.slick-center {
    border-radius: 50%;
    width: 120px;
    height: 120px;
    margin-top: 25px;

    @media screen and (min-width: $mobile-screen) {
      width: 235px;
      margin-top: 0;
      height: 235px;
    }

    .services__img--portals {
      margin-left: 57% !important;
    }

    .services__img--mobile {
      margin-left: 58% !important;
    }

    .services__item-caption {
      display: none;
    }

    .services__img {
      border: 0;
      padding: 0;
      border-radius: 0;
      transition: all 0.5s ease;
      width: 67px;
      height: 67px;
      margin-top: 49.5%;
      margin-left: 50%;
      transform: translateX(-50%) translateY(-50%);

      @media screen and (min-width: $mobile-screen) {
        width: 110px;
        height: 110px;
      }
    }
  }

  .slick-track {
    height: 130px;

    @media screen and (min-width: $mobile-screen){
      height: 230px;
    }
  }

  .services__item-wrapper {
    margin-top: 20px;

    @media screen and (min-width: $mobile-screen){
      margin-top: 57px;
    }
  }

  .services__description-wrapper {

    .slick-list {
      min-height: 390px;
    }
  }

  .services__description-article {
    text-align: center;
    letter-spacing: 0.4px;
  }

  .services__description-heading {
    font-weight: bold;
    font-size: 16px;
    line-height: 41px;
    margin-top: -30px;
  }

  .services__description-text {
    font-size: 16px;
    line-height: 25px;
    max-width: 590px;
    margin: 18px auto 0;
    padding: 0 15px;
  }

  .services__item-caption {
    max-width: 80%;
    margin: 26px auto 0;
    display: none;
    font-size: 16px;
    line-height: 25px;

    @media screen and (min-width: $mobile-screen){
      display: block;
    }
  }

  .services__img {
    width: 65px;
    height: 65px;
    border: 1px solid #54BD82;
    padding: 13px;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
  }
}