.footer {
  background-color: $light-green;
  padding: 35px 15px;

  @media screen and (min-width: $medium-screen) {
    padding: 80px 15px;
  }

  .navigation {
    flex: 0 1 61.5%;
    margin-left: 0;
    display: none;

    @media screen and (min-width: $medium-screen){
      display: block;
    }
  }

  .navigation__link {
    margin-right: 22px;
    color: $grey;

    &:hover {
      color: black;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.footer-top {
  display: flex;
  flex-wrap: wrap;

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
}


.footer-middle,
.footer-bottom {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    margin-top: 80px;
  }
}

.footer-bottom-section {
  flex: 1 0 auto;
}

.copyrights {
  max-width: 19%;
  color: #808080;
  font-size: 16px;
  line-height: 25px;
}

.footer-bottom__policy {
  line-height: 24px;
  margin-right: 18.5%;


  .policy__link {
    margin-top: 23px;
    color: #000;
    font-size: 16px;
    text-decoration: none;
    font-weight: normal;

    &:last-child {
      line-height: 2;
    }

    &:hover {
      text-decoration: underline;
    }
  }
}

.footer-social {
  order: -1;
  flex-basis: 100%;
  display: flex;
  margin-bottom: 40px;

  @media screen and (min-width: $mobile-screen) {
    order: 0;
    margin-bottom: 0;
    flex-basis: auto;
  }
}

.footer-social__icon {
  border: 2px $forest-green solid;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  text-align: center;
  padding: 2.5px;
  margin-left: 10px;
  display: block;

  &:first-child {
    margin-left: 0;
  }

  &:hover {
    background-color: $forest-green;

    path {
      fill: #fff;
    }
  }

  svg {
    display: block;
    margin: 3px auto;
  }

  svg:first-of-type {
    margin-top: 2px;
  }

  &:nth-of-type(2) {
    svg {
      margin-top: 5px;
    }
  }
}

@media screen and (max-width: 600px) {
  .speakers .board_item {
    display: flex!important;
  }
}