.single-case {
  position: fixed;
  top: 5%;
  left: 5%;
  z-index: 10000;
  width: 90vw;
  height: 140%;
  display: none;
  overflow-y: scroll;
  padding: 40px 20px;
  background-color: #fff;
  box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.1);

  .case__icon {
    margin: 20px 10% 0;
    transform: translate(-20px, -20px);
    z-index: 1;
    max-width: 200px;
    max-height: 80px;

    @media screen and (min-width: $mobile-screen){
      margin: 0;
    }
  }

  .case-download:last-of-type {
    @media screen and (min-width: $mobile-screen){
      margin-left: 30px;
    }
  }

  @media screen and (min-width: $tablet-screen) {
    padding: 70px 0 60px 80px;
    height: 85%;
  }

  @media screen and (min-width: $medium-screen) {
    top: 50%;
    transform: translateY(-50%);
    overflow: hidden;
    height: 95%;
    max-height: 700px;
  }
}

.is-shown {
  display: block;
}



.case-info {
  text-align: left;

  @media screen and (min-width: $medium-screen) {
    width: 45%;
    max-width: 700px;
    padding-top: 0;
    margin: 50px 0 0 40px;
  }
}

.case-img {
  height: 250px;
  bottom: 0;
  right: 0;
  width: 100%;
  position: absolute;
  object-fit: cover;

  @media screen and (min-width: $medium-screen) {
    height: 100%;
    max-width: 60%;
    right: -15%;
  }
}

.case-info__heading {
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  margin-top: 41px;
  text-transform: uppercase;
  color: $forest-green;
}

.case-info__text {
  line-height: 1.3;
  letter-spacing: 0.3px;
  margin-top: 14px;
  font-size: 14px;


  @media screen and (min-width: $mobile-screen){
    line-height: 33px;
  }

  &--tech {
    color: $grey;
    margin-top: 20px;

    @media screen and (min-width: $mobile-screen){
      margin-top: 30px;
    }
  }

  strong {
    font-weight: bold;
  }
}

.case-download {
  margin-top: 25px;
  width: 170px;
  display: inline-block;

  @media screen and (min-width: $mobile-screen){
    margin-top: 60px;
  }
}

.case-order {
  margin-top: 30px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 50px;
  }
}

.single-case__close {
  position: absolute;
  right: 40px;
  top: 40px;
  z-index: 1;
  outline: 0;
  cursor: pointer;
  transition: 0.25s ease-out;

  &:hover {
    transform: scale(1.3);
  }

  img {
    filter: brightness(80%);

    @media screen and (min-width: $medium-screen) {
      filter: none;
    }
  }
}
