.thank-you {
  padding: 0 0 130px;

  @media screen and (min-width: $medium-screen) {
    padding: 100px 0;
  }

  .intro {
    &::before {
      content: url('../images/backgrounds/thanks.png');
      overflow: hidden;
    }
  }

  .button--intro {
    margin-top: 100px;
  }

  .intro__img {
    min-height: 550px;
    max-width: 80%;

    &::before {
      content: '';
      width: 100%;
      height: 90%;
      background: $light-green;
    }
  }
}