.tabs{
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: baseline;
  max-width: 300px;
  margin: 30px auto;

  @media screen and (min-width: $tablet-screen){
    align-items: center;
  }

  @media screen and (min-width: $medium-screen) {
    flex-direction: row;
    max-width: 1200px;
    margin: 90px auto 0;
  }
}

.tab-link {
  color: #BDBDBD;
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 0.4px;
  line-height: 25px;
  flex: 1 0 13%;
  cursor: pointer;
  display: flex;
  height: 30px;
  text-align: left;
  justify-content: center;
  transition: all 0.25s;
  margin: 0 0 20px 0;

  @media screen and (min-width: $medium-screen) {
    align-items: center;
  }

    @media screen and (min-width: $medium-screen) {
    height: 70px;
    margin: 0 20px 0 0;
    background-color: #fff;
    border: 1px solid #D1DBE3;
    text-align: center;
    border-radius: 3px;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    color: $forest-green;
  }

  &.is-active {
    color: $forest-green;

    @media screen and (min-width: $medium-screen) {
      border: transparent;
      transform: translateY(-15px);
      box-shadow: 3px 5px 20px rgba(0, 0, 0, 0.1);
    }
  }
}

.tab-content{
  display: none;
  background: #fff;
  margin-top: 40px;
  padding: 15px 20px 140px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 90px;
    padding: 15px 15px 140px;
  }
}

.tab-content.is-active{
  display: flex;
}

.industries-logo-wrapper {
  display: flex;
  padding: 50px 15px 70px;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
  min-height: 400px;
  transition: all 3s;
  width: auto;
  height: auto;
  transform-origin: center center;

  @media screen and (min-width: $tablet-screen){
    justify-content: flex-start;
  }
}

.industries-logo__img {
  display: block;
  flex: 0 1 260px;
  object-fit: contain;
  max-height: 80px;
  cursor: pointer;
  margin: 0 0 40px 0;
  transition: transform 0.5s;
  opacity: 1;

  &.hide {
    opacity: 0;
    position: absolute;
    transform: scale(0);
  }

  @media screen and (min-width: $mobile-screen){
  margin: 0 30px 40px 0;
  }

  &:first-child {
    margin-top: 0;
  }
}

.logo__img {
  max-width: 230px;
  max-height: 80px;
}