.contacts-block {
  padding: 40px 15px 50px;
  text-align: center;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    padding: 85px 15px 230px;
  }
}

.contacts__item {
  color: $grey;
  font-size: 12px;
  display: block;
  line-height: 2;
  max-width: 310px;
  margin: 20px auto 0;

  &:first-child {
    margin-top: 70px;
  }

  &:hover {
    text-decoration: underline;
  }

  &:last-child:hover {
    text-decoration: none;
  }
}

.contacts-form {
  flex: 1 0 30%;
  margin: 50px auto 0;
  max-width: 500px;

  @media screen and (min-width: $medium-screen){
    margin: 80px 0 0;
  }
}

.contacts-wrapper {
  display: flex;
  justify-content: space-between;
}

.contacts__slogan {
  text-align: left;
  flex: 1 0 50%;
  display: none;
  line-height: 1.3;

  @media screen and (min-width: $medium-screen){
    display: block;
  }

    max-width: 505px;
    margin-top: 19%;
}

.contacts__field-label {
  color: $forest-green;
  font-size: 16px;
  margin: 25px 0 6px;
  display: block;
  text-align: left;
  text-transform: capitalize;

  &:first-child {
    margin-top: 0;
  }
}

.contacts-form__field {
  border: 1px solid #D1DBE3;
  border-radius: 5px;
  background: #fff;
  width: 100%;
  height: 40px;
  font-size: 14px;
  padding: 10px;
}

.contacts-form__field--message {
  height: 120px;
  margin-top: 10px;
}

.contacts-form__submit {
  margin: 40px 0 0;

  &:hover {
    background-color: $forest-green;
    color: #fff;
  }
}

.main-page .contacts-block {
  background-color: rgba(128, 128, 128, 0.03);
}

.contacts__email {
  color: $forest-green;
  font-size: 2.25rem;
  line-height: 1.2;
  font-weight: 900;
  margin-top: 13px;
  letter-spacing: 0.45rem;
}

.contacts--extra-padding {
  margin-left: 8%;
}