.list {
  flex: 1 0 65%;
  padding-left: 10%;
}

.list__item {
  margin-top: 15px;
  font-weight: normal;
  position: relative;
  line-height: 1.8;

  &::before {
    content: '';
    position: absolute;
    height: 2px;
    width: 10px;
    background-color: $forest-green;
    left: -30px;
    top: 50%;
    transform: translateY(-50%);
  }
}