.partnership {
  padding: 60px 15px 40px;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    padding: 76px 15px 130px;
  }
}

.partnership-instruments-wrapper {
  display: flex;
  justify-content: space-around;
  margin-top: 35px;
  flex-wrap: wrap;

  @media screen and (min-width: $mobile-small) {
    justify-content: space-between;
  }

  @media screen and (min-width: $medium-screen) {
    margin-top: 55px;
  }
}

.partnership-instruments__item {
  flex: 0 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 165px;
  margin-bottom: 20px;

  @media screen and (min-width: $mobile-small) {

  }
}

.partnership-instruments__text {
  color: $grey;
  margin-top: 50px;
  line-height: 25px;
}

.partnership__proof {
  margin: 50px auto 20px;
  display: block;
}

.partnership__title {
  color: $forest-green;
  font-weight: bold;
  text-align: center;
  margin-top: 15px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 60px;
  }
}

.partnership__caption {
  font-weight: bold;
  text-align: center;
  margin-top: 70px;
  letter-spacing: 2px;
}