.services-page {

  .intro__img--animated,
  .intro__shadow {
    width: 33%;
  }

  .services {
    display: flex;
    padding: 0 0 110px;
    flex-wrap: wrap;
    position: relative;
    margin-top: 30px;

    @media screen and (min-width: $tablet-screen) {
      margin-top: 80px;
    }

    .services__item {
      flex: 1 0 20%;
      text-align: center;
      margin-top: 25px;
      cursor: pointer;
      min-width: 170px;

      @media screen and (min-width: $tablet-screen) {
        margin-top: 85px;
      }

      &:hover {
        .services__item-caption {
          color: #000;
          transform: scale(1.3);
        }
      }
    }

    .services__description-article {
      text-align: center;
      letter-spacing: 0.4px;
    }

    .services__description-heading {
      font-weight: bold;
      font-size: 20px;
      line-height: 41px;
      margin-top: -30px;
    }

    .services__item-caption {
      color: $grey;
      font-weight: 600;
      font-size: 12px;
      margin: 15px auto;
      text-align: center;
      line-height: 2;
      padding-left: 10px;
      z-index: -1;
      transition: all 0.25s linear;

      @media screen and (min-width: $tablet-screen) {
        text-align: left;
        margin: 26px auto 0 28%;
      }
    }

    .services__img {
      width: 65px;
      height: 65px;
      display: block;
      margin: 0 auto;
    }
  }
}