.cases {
}


.case-wrapper {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  padding: 20px 15px 90px;
  position: relative;
  width: auto;
  height: auto;
  visibility: visible;
  transform-origin: center center;
  min-height: 400px;

  @media screen and (min-width: $medium-screen){
    padding: 50px 15px 90px;
  }
}

.case-filters {
  text-align: left!important;
  padding-top: 25px;
  border: 0;
  box-shadow: none!important;
  flex: 1 1 auto!important;
}

.case-filters__item {
  font-weight: bold;
  line-height: 30px;
  color: $grey;
  position: relative;
  padding: 3px;
  cursor: pointer;

  &:hover {
    color: $forest-green;
  }

  &.is-active {
    .case-filters__border {
      border: 1px solid $forest-green;
      color: $forest-green;
    }
  }
}

.case__icon {
  display: block;
  margin-top: 34%;
  margin-left: 50%;
  position: relative;
  z-index: -100;
  transform: translate(-50%, -50%);
}

.case-filters__border {
  padding: 3px 15px;
  border: 1px solid transparent;
  display: inline-block;
}

.case {
  margin: 20px;
  flex: 1 1 auto;
  transition: transform 0.8s ease;
  text-align: center;
  height: 360px;
  position: relative;
  width: 50%;
  border: 1px #D1DBE3 solid;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);

  @media screen and (min-width: $tablet-screen) {
    width: 30%;
  }

  &.hide {
    opacity: 0;
    position: absolute;
    transform: scale(0);
  }

  strong {
    font-weight: bold;
  }

  &:hover {
    cursor: pointer;
    flex: 5 1 auto;

    .button,
    .case__description,
    .case__logo {
      opacity: 1;
      z-index: 10;
    }

    .case__img {
      z-index: -1;
    }

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 2;
      background-color: rgba(255, 255, 255, 0.83);
    }
  }

  .button {
    margin: 47px auto 0;
    object-position: 0;

    &:hover {
      background-color: $forest-green;
      color: #fff;
      box-shadow: 0 20px 40px rgba(0, 145, 131, 0.2);
    }
  }
}

.case__description {
  margin-top: 52px;
  line-height: 24px;
  opacity: 0;
  font-size: 10px;
  position: absolute;
  top: 40px;
  left: 50%;
  width: 280px;
  transform: translateX(-50%);
}

.case__logo {
  height: 60px;
  opacity: 0;
  margin-bottom: 10px;
}

.case__img {
  object-fit: cover;
  height: 100%;
  z-index: -1;
  opacity: 1;
  width: 100%;
}

.tab-link p {
  @media screen and (min-width: $medium-screen) {
    max-width: 140px;
  }
}