.header-wrapper {
  width: 100%;
  padding: 25px 15px 60px;
  z-index: 3;
  height: 40px;
  position: fixed;
  top: 0;
  display: none;
  transition: top 0.2s ease-in-out;

  button {
    outline: 0;
    cursor: pointer;
    transition: transform 0.25s;

    &:hover {
      transform: scale(1.3);
    }
  }

  &.off-canvas  {
    top: -200px;
  }

  .fixed {
    top: 0;
    z-index: 9999;
  }

  @media screen and (min-width: $medium-screen){
    display: block;
  }

}

.header {
  display: flex;
  flex-wrap: wrap;
  width: 95vw;
  max-width: 1700px;
  margin: 0 auto;


  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .policy__link {
    margin-top: 23px;
    font-size: 12px;
  }
}

.language {
  position: relative;


  img {
    margin-left: 5px;
  }
}

.language-btn {
  width: 70px;
}

.is-hidden {
  position: absolute;
  top: 60px;
  display: none;
  left: 15px;
}

.is-rotated {
  transform: rotate(180deg);
  transition: all 0.2s ease;
}

.language__option {
  color: $forest-green;
  cursor: pointer;
}

.header-wrapper--main {

  > .container {
    max-width: 1200px!important;
  }
}

.language {
  .button {
    max-width: 70px;
  }
}
.header-wrapper > .container {
  padding: 0 15px;
}